import { useState } from "react";
import { useFinanceTransactionHistoryRemoveCard } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { useTranslation } from "react-i18next";
import ModalMenu from "../../../Ui/Modal/ModalMenu";

const FinanceReceiptCardMenu = ({ onClose, frid, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [financeTransactionHistoryRemoveCard] =
    useFinanceTransactionHistoryRemoveCard();

  const onDelete = () => {
    if (frid) {
      setDisabled((pre) => !pre);
      financeTransactionHistoryRemoveCard({
        frid: frid,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <ModalMenu
        onClose={onClose}
        disabled={disabled}
        customStyle={{
          width: "18rem",
        }}
      >
        <h6 onClick={onDelete}>{t("delete")}</h6>
      </ModalMenu>
    </>
  );
};

export default FinanceReceiptCardMenu;
